module.exports = "<template>\n<div>\n  <div class=\"my-3\">\n    <v-alert description=\"使用node-key作为value，请保证唯一性， 另外， 不需要指定完整路径， 指定最后一级即可\"></v-alert>\n  </div>\n  <div class=\"my-3\">\n    <v-cascader node-key=\"key\" :data-source=\"dataSource\" v-model=\"value\" clearable></v-cascader>\n  </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { VForm, VTree } from 'src'\n\n/**\n * @title   基本示例\n * @desc 基本示例。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class BasicExample extends Vue {\n  value: Array<string | number> = ['2-2-1']\n\n  dataSource = [{\n    key: '1',\n    label: '一级 1',\n    disabled: true,\n    children: [{\n      key: '1-1',\n      label: '二级 1-1',\n      children: [{\n        key: '1-1-1',\n        label: '三级 1-1-1'\n      }]\n    }]\n  }, {\n    key: '2',\n    label: '一级 2',\n    children: [{\n      key: '2-1',\n      label: '二级 2-1',\n      children: [{\n        key: '2-1-1',\n        label: '三级 2-1-1'\n      }]\n    }, {\n      key: '2-2',\n      label: '二级 2-2',\n      children: [{\n        key: '2-2-1',\n        label: '三级 2-2-1'\n      }]\n    }]\n  }, {\n    key: '3',\n    label: '一级 3',\n    children: [{\n      disabled: true,\n      key: '3-1',\n      label: '二级 3-1',\n      children: [{\n        key: '3-1-1',\n        label: '三级 3-1-1'\n      }]\n    }, {\n      key: '3-2',\n      label: '二级 3-2',\n      children: [{\n        key: '3-2-1',\n        label: '三级 3-2-1'\n      }]\n    }]\n  }]\n}\n</script>\n"