




































































import { Component, Vue, Watch } from 'vue-property-decorator'
import { VForm } from 'src'

/**
 * @title   校验
 * @desc 表单校验。
 */
@Component({
  components: {
  },
  })
export default class ValidExample extends Vue {
  form = {
    username: 'Alison',
    password: '',
    sex: 'M',
    skill: ['Js'],
    city: 'shanghai',
    birthday: '1992/08/29',
    tel: ['', ''],
    profession: []
  }

  rules = {
    username: [
      {validator: 'required', message: '用户名必填', trigger: 'blur'}
    ],
    password: [
      {validator: 'required'},
      {validator: 'length', min: 6, max: 8},
    ],
    skill: [
      {validator: 'required'},
      {validator: 'length', min: 1},
    ],
    city: [
       {validator: 'required'},
    ],
    birthday: [
       {validator: 'required'},
    ],
    tel: [
       {validator: (rule: any, value: any) => {
         return new Promise((resolve, reject) => {
           setTimeout(() => {
             if (value && value[0] && value[1]) {
               return resolve()
             }
             return reject(new Error('区号、电话必填'))
           }, 2000)
         })
       }},
    ],
    profession: [
      {validator: 'length', min: 1, message: '请选择行业'},
    ]
  }

  labelPosition: string = 'right'

  layout: string = 'vertical'

  professions = [
    {
      key: '1',
      label: '工业',
      children: [
        {
          key: '1-1',
          label: '采矿业',
        },
        {
          key: '1-2',
          label: '制造业',
        }
      ]
    },
    {
      key: '2',
      label: '商业',
      children: [
        {
          key: '2-1',
          label: '批发零售',
        },
        {
          key: '2-2',
          label: '汽车销售',
        }
      ]
    }
  ]

  submit () {
    this.$refs.form.validate().then(({ valid, errors }) => {
      if (valid) {
        this.$vua.$message.success('验证成功')
      } else {
        this.$vua.$message.error('验证失败')
        console.log('errors: ', errors)
      }
    })
  }

  reset () {
    this.$refs.form.resetFields()
  }

  $refs!: {
    form: VForm
  }
}
