


















import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title  自动调整字符大小
 * @desc 对于字符型的头像，当字符串较长时，字体大小可以根据头像宽度自动调整。
 */
@Component({
  components: {
  },
  })
export default class TextAdjustExample extends Vue {
  size: string = "md"

  index: number = 0

  list = [
    {
      color: 'orange',
      text: 'U'
    },
    {
      color: 'purple',
      text: 'Lucy'
    },
    {
      color: 'Tom',
      text: 'yellow'
    },
    {
      color: 'info',
      text: 'Spencer'
    },
    {
      color: 'error',
      text: '王二牛'
    }
  ]

  change () {
    let len = this.list.length
    if (this.index < len - 1) this.index++
    else this.index = 0
  }
}
