module.exports = "<template>\n<div>\n  <div class=\"my-3\">\n    <v-tree node-key=\"key\" default-expand-all :data-source=\"dataSource\" ref=\"tree\">\n      <template slot=\"indicator\" slot-scope=\"{node}\">\n        <i class=\"anticon anticon-folder\" v-if=\"!node.isLeaf\"></i>\n        <i class=\"anticon anticon-file\" v-else></i>\n      </template>\n\n      <div slot=\"content\"  slot-scope=\"{node}\" class=\"d-flex justify-content-between\">\n        <span class=\"mr-4\">{{node.data.label}}</span>\n        <span>\n          <a class=\"mr-2\" @click=\"append(node)\">添加</a>\n          <a @click=\"del(node)\">删除</a>\n        </span>\n      </div>\n    </v-tree>\n  </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { VForm, VTree, Node, TreeNodeFilterFn } from '@/index'\n\nlet index = 1000\n\n/**\n * @title 自定义图标和内容\n * @desc 可以针对不同的节点定制图标和内容。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class IconContentExample extends Vue {\n  dataSource = [{\n    key: '1',\n    label: '一级 1',\n    disabled: true,\n    children: [{\n      key: '1-1',\n      label: '二级 1-1',\n      children: [{\n        key: '1-1-1',\n        label: '三级 1-1-1'\n      }]\n    }]\n  }, {\n    key: '2',\n    label: '一级 2',\n    children: [{\n      key: '2-1',\n      label: '二级 2-1',\n      children: [{\n        key: '2-1-1',\n        label: '三级 2-1-1'\n      }]\n    }, {\n      key: '2-2',\n      label: '二级 2-2',\n      children: [{\n        key: '2-2-1',\n        label: '三级 2-2-1'\n      }]\n    }]\n  }, {\n    key: '3',\n    label: '一级 3',\n    children: [{\n      disabled: true,\n      key: '3-1',\n      label: '二级 3-1',\n      children: [{\n        key: '3-1-1',\n        label: '三级 3-1-1'\n      }]\n    }, {\n      key: '3-2',\n      label: '二级 3-2',\n      children: [{\n        key: '3-2-1',\n        label: '三级 3-2-1'\n      }]\n    }]\n  }]\n\n  append (node: Node) {\n    if (!node.data.children) {\n      this.$set(node.data, 'children', [])\n    }\n    node.data.children.push({\n      key: index++,\n      label: 'label' + index\n    })\n    node.expandNode(true)\n  }\n\n  del (node: Node) {\n    if (node.parent) {\n      let children = node.parent.data.children || node.parent.data\n      let idx = children.findIndex((v: any) => node.data.key === v.key)\n      if (idx >= 0) children.splice(idx, 1)\n    }\n  }\n\n  $refs!: {\n    tree: VTree\n  }\n}\n</script>\n"