













import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title 自定义输入组件
 * @desc 自定义输入组件。
 */
@Component({
  components: {
  },
  })
export default class CompExample extends Vue {
  value: string = ''

  value1: string = ''

  clob: string[] = [
    'a', 'a1', 'a2', 'a3', 'a4',
    'b', 'b1', 'b2', 'b3', 'b4',
    'c', 'c1', 'c2', 'c3', 'c4',
  ]

  searchFn (input: string, cb: (items: any[])=>void) {
    setTimeout(() => {
      let ret = this.clob.filter(v => v.includes(input)).map((v, i) => {
        return {
          title: v
        }
      })
      cb(ret)
    }, 800)
  }
}
