















import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title 包含子组件
 * @desc 加载占位图包含子组件。
 */
@Component({
  components: {
  },
  })
export default class LoadingExample extends Vue {
  loading: boolean = false

  load () {
