module.exports = "<template>\n<div>\n  <div class=\"my-3\">\n    <v-alert class=\"mb-3\" type=\"warning\" description=\"默认关联选中， 即选择父节点， 其子节点也相应选上；所有子节点选中， 父节点也相应选中\"></v-alert>\n    <v-checkbox v-model=\"checkStrictly\" class=\"mr-2\">是否取消关联选中</v-checkbox>\n    <v-button color=\"primary\" @click=\"select\">选中 1-1、2-1</v-button>\n  </div>\n  <div class=\"my-3\">\n    <v-tree node-key=\"key\" :checkStrictly=\"checkStrictly\" checkable :data-source=\"dataSource\" ref=\"tree\"></v-tree>\n  </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { VForm, VTree } from 'src'\n\n/**\n * @title  可选择\n * @desc 树节点的选择。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class BasicExample extends Vue {\n  checkStrictly: boolean = false\n\n  dataSource = [{\n    key: '1',\n    label: '一级 1',\n    disabled: true,\n    children: [{\n      key: '1-1',\n      label: '二级 1-1',\n      children: [{\n        key: '1-1-1',\n        label: '三级 1-1-1'\n      }]\n    }]\n  }, {\n    key: '2',\n    label: '一级 2',\n    children: [{\n      key: '2-1',\n      label: '二级 2-1',\n      children: [{\n        key: '2-1-1',\n        label: '三级 2-1-1'\n      }]\n    }, {\n      key: '2-2',\n      label: '二级 2-2',\n      children: [{\n        key: '2-2-1',\n        label: '三级 2-2-1'\n      }]\n    }]\n  }, {\n    key: '3',\n    label: '一级 3',\n    children: [{\n      disabled: true,\n      key: '3-1',\n      label: '二级 3-1',\n      children: [{\n        key: '3-1-1',\n        label: '三级 3-1-1'\n      }]\n    }, {\n      key: '3-2',\n      label: '二级 3-2',\n      children: [{\n        key: '3-2-1',\n        label: '三级 3-2-1'\n      }]\n    }]\n  }]\n\n  select () {\n    this.$refs.tree.setCheckedKeys(['1-1', '2-1'])\n  }\n\n  $refs!: {\n    tree: VTree\n  }\n}\n</script>\n"