module.exports = "<template>\n<div>\n  <v-radio-group v-model=\"size\">\n    <v-radio-button label=\"lg\">大</v-radio-button>\n    <v-radio-button label=\"md\">中</v-radio-button>\n    <v-radio-button label=\"sm\">小</v-radio-button>\n  </v-radio-group>\n\n  <v-radio-group v-model=\"border\" class=\"mx-2\">\n    <v-radio-button label=\"0\">无边框</v-radio-button>\n    <v-radio-button label=\"1\">有边框</v-radio-button>\n  </v-radio-group>\n\n  <v-alert class=\"my-2\" :description=\"`已选择 ${count} 项`\"></v-alert>\n\n   <div class=\"m-3\">\n     <v-table row-key=\"key\" pageable :bordered=\"bordered\" :size=\"size\" :data-source=\"dataSource\" @selection-change=\"selectionChange\">\n       <v-table-column type=\"selection\" :selections=\"selections\"></v-table-column>\n       <v-table-column type=\"index\" label=\"#\"></v-table-column>\n       <v-table-column prop=\"name\" label=\"姓名\"></v-table-column>\n       <v-table-column prop=\"age\" label=\"年龄\"></v-table-column>\n       <v-table-column prop=\"address\" label=\"地址\"></v-table-column>\n       <v-table-column column-key=\"opt\" label=\"操作\">\n         <template slot-scope=\"{}\">\n           <v-button type=\"text\">编辑</v-button> | <v-button type=\"text\">删除</v-button>\n         </template>\n       </v-table-column>\n     </v-table>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { TableColumnSelectionCbParam } from '../../../../../../src/components/type'\n\n/**\n * @title  可选择\n * @desc 选择多行数据时使用 Checkbox, 注意指定row-key。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class SelectionExample extends Vue {\n  dataSource = [ { key: 1, name: '易平', age: 85, address: '陕西省 咸阳市 彬县' },\n  { key: 2, name: '方洋', age: 42, address: '山西省 朔州市 怀仁县' },\n  { key: 3, name: '钱勇', age: 92, address: '上海 上海市 杨浦区' },\n  { key: 4, name: '钱超', age: 97, address: '河北省 唐山市 丰润区' },\n  { key: 5, name: '谭娟', age: 95, address: '江西省 萍乡市 莲花县' },\n  { key: 6, name: '魏霞', age: 86, address: '澳门特别行政区 澳门半岛' },\n  { key: 7, name: '郝艳', age: 29, address: '四川省 遂宁市 其它区' },\n  { key: 8, name: '万军', age: 55, address: '贵州省 毕节市 金沙县' },\n  { key: 9, name: '江洋', age: 31, address: '内蒙古自治区 锡林郭勒盟 正镶白旗' },\n  { key: 10, name: '余涛', age: 92, address: '重庆 重庆市 巴南区' },\n  { key: 11, name: '陈洋', age: 42, address: '河北省 秦皇岛市 昌黎县' } ]\n\n  size: string = 'lg'\n\n  border: string = '0'\n\n  get bordered () {\n    return this.border !== '0'\n  }\n\n  count: number = 0\n\n  selections = [\n    {\n      label: '全部',\n      key: 'all',\n      onSelect: ({allData, selectionKeySet}: TableColumnSelectionCbParam) => {\n        allData.forEach(v => {\n          selectionKeySet.add(v.key)\n        })\n      }\n    },\n    {\n      label: '奇数行',\n      key: 'odd',\n      onSelect: ({currentData, selectionKeySet}: TableColumnSelectionCbParam) => {\n        currentData.forEach((v, i) => {\n          if (i % 2 === 0) {\n            selectionKeySet.add(v.key)\n          } else {\n            selectionKeySet.delete(v.key)\n          }\n        })\n      }\n    },\n    {\n      label: '清空',\n      key: 'clear',\n      onSelect: ({selectionKeySet}: TableColumnSelectionCbParam) => {\n        selectionKeySet.clear()\n      }\n    }\n  ]\n\n  selectionChange (keys: any[]) {\n    this.count = keys.length\n  }\n}\n</script>\n"