module.exports = "<template>\n<div>\n  <div class=\"my-3\">\n    <v-checkbox v-model=\"accordion\">手风琴模式</v-checkbox>\n\n    <v-checkbox v-model=\"checkable\">可选择</v-checkbox>\n\n    <v-button class=\"ml-2\" color=\"primary\" @click=\"expandAll\">展开/收起全部</v-button>\n  </div>\n  <div class=\"my-3\">\n    <v-tree node-key=\"key\" :data-source=\"dataSource\" :accordion=\"accordion\" :checkable=\"checkable\" ref=\"tree\"></v-tree>\n  </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { VForm, VTree } from 'src'\n\n/**\n * @title   基本示例\n * @desc 基本示例。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class BasicExample extends Vue {\n  accordion: boolean = false\n\n  checkable: boolean = false\n\n  expand: boolean = false\n\n  dataSource = [{\n    key: '1',\n    label: '一级 1',\n    disabled: true,\n    children: [{\n      key: '1-1',\n      label: '二级 1-1',\n      children: [{\n        key: '1-1-1',\n        label: '三级 1-1-1'\n      }]\n    }]\n  }, {\n    key: '2',\n    label: '一级 2',\n    children: [{\n      key: '2-1',\n      label: '二级 2-1',\n      children: [{\n        key: '2-1-1',\n        label: '三级 2-1-1'\n      }]\n    }, {\n      key: '2-2',\n      label: '二级 2-2',\n      children: [{\n        key: '2-2-1',\n        label: '三级 2-2-1'\n      }]\n    }]\n  }, {\n    key: '3',\n    label: '一级 3',\n    children: [{\n      disabled: true,\n      key: '3-1',\n      label: '二级 3-1',\n      children: [{\n        key: '3-1-1',\n        label: '三级 3-1-1'\n      }]\n    }, {\n      key: '3-2',\n      label: '二级 3-2',\n      children: [{\n        key: '3-2-1',\n        label: '三级 3-2-1'\n      }]\n    }]\n  }]\n\n  expandAll () {\n    this.expand = !this.expand\n    this.$refs.tree.expandAll(this.expand)\n  }\n\n  $refs!: {\n    tree: VTree\n  }\n}\n</script>\n"