module.exports = "<template>\n<div>\n  <div class=\"my-3\">\n    <v-input v-model=\"value\" placeholder=\"search\"></v-input>\n  </div>\n  <div class=\"my-3\">\n    <v-tree node-key=\"key\" default-expand-all :data-source=\"dataSource\" :filter-fn=\"filterFn\" ref=\"tree\"></v-tree>\n  </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\nimport { VForm, VTree, TreeNodeFilterFn } from '@/index'\n\n/**\n * @title 可搜索\n * @desc 可搜索的树。\n */\n@Component({\n  components: {\n  },\n  })\nexport default class FilterExample extends Vue {\n  value: string = ''\n\n  dataSource = [{\n    key: '1',\n    label: '一级 1',\n    disabled: true,\n    children: [{\n      key: '1-1',\n      label: '二级 1-1',\n      children: [{\n        key: '1-1-1',\n        label: '三级 1-1-1'\n      }]\n    }]\n  }, {\n    key: '2',\n    label: '一级 2',\n    children: [{\n      key: '2-1',\n      label: '二级 2-1',\n      children: [{\n        key: '2-1-1',\n        label: '三级 2-1-1'\n      }]\n    }, {\n      key: '2-2',\n      label: '二级 2-2',\n      children: [{\n        key: '2-2-1',\n        label: '三级 2-2-1'\n      }]\n    }]\n  }, {\n    key: '3',\n    label: '一级 3',\n    children: [{\n      disabled: true,\n      key: '3-1',\n      label: '二级 3-1',\n      children: [{\n        key: '3-1-1',\n        label: '三级 3-1-1'\n      }]\n    }, {\n      key: '3-2',\n      label: '二级 3-2',\n      children: [{\n        key: '3-2-1',\n        label: '三级 3-2-1'\n      }]\n    }]\n  }]\n\n  filterFn: TreeNodeFilterFn = ({node, value}) => {\n    if (!value) return true\n    return node.data.label.indexOf(value) !== -1\n  }\n\n  @Watch('value') valueChange (value: any) {\n    this.$refs.tree.filter(value)\n  }\n\n  $refs!: {\n    tree: VTree\n  }\n}\n</script>\n"