module.exports = "<template>\n<div>\n    <div class=\"my-4\">\n        <p class=\"title\">块</p>\n        <div class=\"d-flex\">\n                <div class=\"border-right\" style=\"width: 50%;\">\n                    <pre>\n@include b(v-button){\n    color: #fff;\n}\n                    </pre>\n                </div>\n                <div>\n                    <pre>\n    .v-button {\n        color: #fff;\n    }\n                    </pre>\n                </div>\n        </div>\n    </div>\n\n    <div class=\"my-4\">\n        <p class=\"title\">元素</p>\n        <div class=\"d-flex\">\n                <div class=\"border-right\" style=\"width: 50%;\">\n                    <pre>\n@include b(v-button){\n    color: #fff;\n    @include e(text){\n        color: #000;\n    }\n}\n                    </pre>\n                </div>\n                <div>\n                    <pre>\n    .v-button {\n        color: #fff;\n    }\n    .v-button__text {\n        color: #000;\n    }\n                    </pre>\n                </div>\n        </div>\n    </div>\n\n    <div class=\"my-4\">\n        <p class=\"title\">修饰符</p>\n        <div class=\"d-flex\">\n                <div class=\"border-right\" style=\"width: 50%;\">\n                    <pre>\n@include b(v-button){\n    @include m(dark){\n        color: black;\n        @include e(text){\n            color: white;\n        }\n    }\n    @include e(text){\n        color: #000;\n        @include m(primary){\n            color: red;\n        }\n    }\n}\n                    </pre>\n                </div>\n                <div>\n                    <pre>\n    .v-button--dark {\n        color: black;\n    }\n    .v-button--dark .v-button__text {\n        color: white;\n    }\n\n    .v-button__text {\n        color: #000;\n    }\n    .v-button__text--primary {\n        color: red;\n    }\n                    </pre>\n                </div>\n        </div>\n    </div>\n\n    <div class=\"my-4\">\n        <p class=\"title\">组合</p>\n        <div class=\"d-flex\">\n                <div class=\"border-right\" style=\"width: 50%;\">\n                    <pre>\n@include b(v-button){\n    @include m(dark){\n        &.active{\n        color: #fff;\n        }\n    }\n\n    @include m(dark, lg){\n        &.hover{\n        color: black;\n        }\n    }\n\n    @include e(text){\n        @include m(lg, sm){\n            &.active{\n                color: #000;\n            }\n        }\n    }\n}\n\n                    </pre>\n                </div>\n                <div>\n                    <pre>\n    .v-button--dark.active {\n        color: #fff;\n    }\n\n    .v-button--dark.v-button--lg.hover {\n        color: black;\n    }\n\n    .v-button__text--lg.v-button__text--sm.active {\n        color: #000;\n    }\n                    </pre>\n                </div>\n        </div>\n    </div>\n\n<div class=\"my-4\">\n        <p class=\"title\">嵌套</p>\n        <div class=\"d-flex\">\n                <div class=\"border-right\" style=\"width: 50%;\">\n                    <pre>\n@include b(v-button){\n    &>{\n        @include e(text, true){\n        color: #fff;\n        }\n    }\n\n    @include b(menu){\n        color: black;\n        @include e(item, true){\n            color: #fff;\n            @include m(dark){\n                color: #fff;\n            }\n        }\n    }\n\n    @include e(addon){\n        color: green;\n    }\n}\n\n                    </pre>\n                </div>\n                <div>\n                    <pre>\n    .v-button > .v-button__text {\n        color: #fff;\n    }\n\n    .v-button .menu {\n        color: black;\n    }\n    .v-button .menu .menu__item {\n        color: #fff;\n    }\n    .v-button .menu .menu__item--dark {\n        color: #fff;\n    }\n\n    .v-button__addon {\n        color: green;\n    }\n                    </pre>\n                </div>\n        </div>\n    </div>\n\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title   基本使用示例\n * @desc 针对组件的各种使用情况示例\n */\n@Component({\n  components: {\n  },\n  })\nexport default class BasicExample extends Vue {\n}\n</script>\n"